*{
  margin: 0;
  padding: 0;
}

.row {
  display: flex;
  flex-direction: row;
}

.NavBar {
  min-height: 100px;
  background-color: black;
  align-items: center;
  width: 100%;
  display: flex;
  flex-direction: row;
  font-size: 25px;
  font-weight: 300;
  border-bottom: solid cyan 5px;
}

.an a {
  padding: 10px;
  color: cyan;
  text-decoration: none;
  font-size: 25px;
}

.an :hover{
  color: cyan;
  font-size: 30px;
  text-decoration: underline cyan 1px;
}

.Content{
  background-color: black;
  height: max-content;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  padding: 100px;
  border-bottom: solid 5px cyan;
}

.Content h1,h2{
  color: cyan;
}

.Me img{
  border: solid cyan 3px;
  min-height: 200px;
  width: 200px;
  border-radius: 100px;
}

.Footer{
  display: flex;
  flex-direction: row;
  background-color:black;
  min-height: 100px;
  width: 100%;
  align-items: center;
  justify-content: center;
  overflow-y: hidden;
  display: flex;
  flex-direction: row;
  color: cyan;
}
.Footer div{
  padding: 10px;
}

.Footer div:hover{
  text-decoration: underline;
}

.Footer img{
  background-color: cyan;
  transition: transform .7s ease-in-out;
}

.Footer img:hover{
  transform: rotate(30deg);
}

.Top{
  min-height: 100px;
  width: 100%;
  background-color: black;
  text-align: center;
}

.Top h1{
  color: cyan;
}